import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import * as serviceWorker from './serviceWorker';
import VideoControl from './VideoControl'; 

const testTrackData = [
  {name: "Eka kokeilu 2", actions: "delete, submit, pitch, timbre, tempo", status: "Draft", score: "34/100", selectable: true},
  {name: "Mun paras veto", actions: "delete, pitch, timbre, tempo", status: "Submitted", score: "64/100", selectable: false}
];

const testSubmittedTrackData = [
  {artist: "Ms Sunshine 2", name: "Mun paras", score: "75/100", selectable: true},
  {artist: "Setämies", name: "Tällä mennään", score: "26/100", selectable: false},
  {artist: "Diesel-Altto", name: "Asialliset alaäänet", score: "65/100", selectable: true},
  {artist: "Director", name: "Piano track", score: "-", selectable: true}
];

function Header() {
  return (
    <div className="header row">
      <div className="col-10">
        Choose production: Kuulan Rukous - the best track ever
      </div>
      <div className="col-2">
        <div className="row">
          <div className="col">
            Logged in: Uuseri
          </div>
        </div>
        <div className="row">
          <div className="col">
            Voice: B2
          </div>
        </div>
      </div>
    </div>
  );
}

const TrackDataList = (props) => (
  <div className="listComponent">
    <div className="row listHeader">
      <div className="col">
        Trackname
      </div>
      <div className="col">
        Actions
      </div>
      <div className="col">
        Status
      </div>
      <div className="col">
        Score
      </div>
      <div className="col">
        Select for play
      </div>
    </div>
    {props.tracks.map(track => <TrackDataRow {...track} />)}
  </div>
);

const TrackDataRow = (props) => (
  <div className="row">
    <div className="col">
      {props.name}
    </div>
    <div className="col">
      {props.actions}
    </div>
    <div className="col">
      {props.status}
    </div>
    <div className="col">
      {props.score}
    </div>
    <div className="col">
      {props.selectable}
    </div>
  </div>
);

const SubmittedTrackList = (props) => (
  <div className="listComponent">
    <div className="row listHeader">
      <div className="col">
        Artist
      </div>
      <div className="col">
        Name
      </div>
      <div className="col">
        Score
      </div>
      <div className="col">
        Select for play
      </div>
    </div>
    {props.tracks.map(track => <SubmittedTrackRow {...track} />)}
  </div>
);

const SubmittedTrackRow = (props) => (
  <div className="row">
    <div className="col">
      {props.artist}
    </div>
    <div className="col">
      {props.name}
    </div>
    <div className="col">
      {props.score}
    </div>
    <div className="col">
      {props.selectable}
    </div>
  </div>
);

function MainContent(props) {
  return (
    <div className="row">
      <div className="col-8">
        <div className="row">
          <div className="col">
            <TrackDataList tracks={props.state.trackList} />
            <SubmittedTrackList tracks={props.state.submittedTrackList} />
          </div>
        </div>
      </div>
      <div className="col-4">
         <VideoControl />  
      </div>
    </div>
  );
}

function Footer() {
  return (
    <div className="row">
      <div className="col">
        Copyright 2020 by <b>LaulApp</b>. All rights reserved.
      </div>
    </div>
  );
}

class MainApp extends React.Component {
  state = {
    trackList: testTrackData,
    submittedTrackList: testSubmittedTrackData
  };

  render() {
    return (
      <div className="container">
        <h2>Laulapp</h2>
        <Header />
        <MainContent state={this.state} />
        <Footer /> 
      </div>
    );
  }
}

ReactDOM.render(
  // <React.StrictMode>
  //   What does this do? <MainApp />
  // </React.StrictMode>,
  <MainApp />,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
