import React, { useState, useEffect } from 'react';
import './VideoControl.css';
//import testVideoImage from './images/testVideoImage.png'
import videoRecordButton from './images/videoRecordButton.png'
//import videoPlayButton from './images/videoPlayButton.png'
import videoStopButton from './images/videoStopButton.png'
import videoDownloadButton from './images/videoDownloadButton.png'
import axios from 'axios';
import MicRecorder from 'mic-recorder-to-mp3';

const Mp3Recorder = new MicRecorder({ bitRate: 128 });

//let recordingTimeMS = 5000;
//const apiBaseUrl = 'http://localhost:3001'
const apiBaseUrl = process.env.NODE_ENV === 'development'
  ? 'http://localhost:3001'
  : ''



function VideoControl() {
 
  const [downloadButton, setDownloadButton] = useState({})
  const [users, setUsers] = useState([])
  const [currentUser, setCurrentUser] = useState({
    id: "1234",
    name: "Monster68"
  })
  const [productions, setProductions] = useState([])
  const [tracks, setTracks] = useState([])
  const [blob, setBlob] = useState(null)
  const [videoMode, setVideoMode] = useState('recording')
  const [audioState, setAudioState] = useState({
    isRecording: false,
    blobURL: '',
    isBlocked: false,
  })
  const [trackInfo, setTrackInfo] = useState({ trackName: '' })
  const [playProduction, setPlayProduction] = useState('')
  const [playVideoTrack, setPlayVideoTrack] = useState('') 
  const [playAudioTrackList, setPlayAudioTrackList] = useState({})
  const [deleteTrackList, setDeleteTrackList] = useState({}) 
  const [notification, setNotification] = useState(null)

  const isVideo = (track) => {
    //console.log('SPLITTI', track.name, track.url.split('.')[1])
    return track.url.split('.')[1] !== 'mp3'
  }
  //const getProductionByID = (productionId) => productions.find(production => production.id == productionId)
  const getProductionsTracks = (productionId) => tracks.filter(track => track.productionId == productionId)
  const getTrackByID = (trackId) => tracks.find(track => track.id == trackId)
  const trackLabel = (track) => track.name + (isVideo(track) ? '-video' : '')

  const fetchData = async () => {
    const { data: users } = await axios.get(apiBaseUrl + '/api/users')
    setUsers(users)
    const { data: productions } = await axios.get(apiBaseUrl + '/api/productions')
    setProductions(productions)
    const { data: tracks } = await axios.get(apiBaseUrl + '/api/tracks')
    setTracks(tracks)
  }

  useEffect(() => {
    navigator.getUserMedia({ audio: true, video: true },
      () => {
        console.log('Permission Granted');
        setAudioState({ ...audioState, isBlocked: false });
      },
      () => {
        console.log('Permission Denied');
        setAudioState({ ...audioState, isBlocked: true })
      },
    );
    fetchData()
  }, [])

  console.log('USERS:', users, 'PRODUCTIONS: ', productions, 'TRACKS: ', tracks)
  console.log('TRACKINFO', trackInfo)
  console.log('PLAY PRODUCTION & TRACK', playProduction, playVideoTrack)
  console.log('PLAY AUDIO TRACK LIST', playAudioTrackList)


  function log(msg) {
    console.log('------ ' + msg)
  }
  /* function wait(delayInMS) {
    return new Promise(resolve => setTimeout(resolve, delayInMS));
  } */
  const notify = (message, type) => {
    setNotification({ message, type })
    setTimeout(() => setNotification(null), 2000)
  }

  function startVideoRecording(stream, lengthInMS) {
    let recorder = new MediaRecorder(stream);
    let data = [];

    recorder.ondataavailable = event => data.push(event.data);
    recorder.start();
    log(recorder.state + " for " + (lengthInMS / 1000) + " seconds...");

    let stopped = new Promise((resolve, reject) => {
      recorder.onstop = resolve;
      recorder.onerror = event => reject(event.name);
    });
    /* let recorded = wait(lengthInMS).then(
      () => recorder.state == "recording" && recorder.stop()
    ); */
    return Promise.all([
      stopped,
      // recorded
    ])
      .then(() => data);
  }

  function stopVideoRecording(stream) {
    stream.getTracks().forEach(track => track.stop());
  }

  const handleStartVideoRecording = () => {
    setPlayProduction('')
    setPlayVideoTrack('')
    setBlob(null)
    setVideoMode('recording')
    let recording = document.getElementById("recording");
    navigator.mediaDevices.getUserMedia({
      video: true,
      audio: true
    }).then(stream => {
      console.log('STREAM', stream)
      recording.srcObject = stream;
      setDownloadButton({ ...downloadButton, href: stream })
      recording.captureStream = recording.captureStream || recording.mozCaptureStream;
      return new Promise(resolve => recording.onplaying = resolve);
    }).then(() => startVideoRecording(recording.captureStream()/* , recordingTimeMS */))
      .then(recordedChunks => {
        let recordedBlob = new Blob(recordedChunks, { type: "video/webm" });
        console.log('BLOBBI', recordedBlob)
        setBlob(recordedBlob)
        let playLocal = document.getElementById("playLocal");
        playLocal.src = URL.createObjectURL(recordedBlob);
        setDownloadButton({ download: "RecordedVideo.webm", href: playLocal.src })
        log("Successfully recorded " + recordedBlob.size + " bytes of " +
          recordedBlob.type + " media.");
      })
      .catch(log);
  }
  const handleStopVideoRecording = () => {
    let recording = document.getElementById("recording");
    stopVideoRecording(recording.srcObject);
    setVideoMode('playLocal')
  }

  const handleUploadRecording = async () => {
    console.log('UPLOADING')
    const name = productions.find(production => production.id == trackInfo.productionId).name + '_' + trackInfo.trackName
    const file = new File([blob], name, { lastModified: new Date(), type: blob.type });
    console.log('UUSI FILEE', file)
    const formData = new FormData();
    formData.append('file', file)

    try {
      let options = {
        url: apiBaseUrl + '/api/files',
        method: 'POST',
        data: formData,
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'multipart/form-data',
        },
      };
      const { data: newFile } = await axios(options)
      console.log('NEW FILE', newFile.data)
      const { data: newTrack } = await axios.post(apiBaseUrl + '/api/tracks', {
        url: newFile.data.url,
        userId: currentUser.id,
        productionId: trackInfo.productionId,
        name: trackInfo.trackName
      })
      console.log('NEW TRACK', newTrack)
      setTracks(tracks.concat(newTrack))
      notify('NEW TRACK SAVED TO DATABASE ', 'success')
      setTrackInfo({ trackName: '' })
    } catch (error) {
      notify('SAVE NEW TRACK ERROR' + error, 'error')
    }
  }

  const handlePlayRecording = (trackId) => {
    setBlob(null)
    const chosenTrack = getTrackByID(trackId)
    console.log('PLAYING RECORDING', chosenTrack)
    if (isVideo(chosenTrack)) { 
      setVideoMode('playRemote')
      let playRemote = document.getElementById("playRemote");
      playRemote.src = apiBaseUrl + chosenTrack.url
    } else { 
      let audioRecorder = document.getElementById("audioRecorder");
      audioRecorder.src = apiBaseUrl + chosenTrack.url;
      audioRecorder.autoplay = true
    }
  }

  const handleStartAudioRecording = () => {
    setPlayProduction('') 
    let audioRecorder = document.getElementById("audioRecorder");
    audioRecorder.autoplay = false
    setBlob(null)
    if (audioState.isBlocked) {
      console.log('Permission Denied');
    } else {
      Mp3Recorder
        .start()
        .then(() => {
          setAudioState({ ...audioState, isRecording: true });
        }).catch((e) => console.error(e));
    }
  };

  const handleStopAudioRecording = () => {
    Mp3Recorder
      .stop()
      .getMp3()
      .then(([buffer, recordedBlob]) => {
        setBlob(recordedBlob)
        console.log('AUDIO BLOBBI', recordedBlob)
        const blobURL = URL.createObjectURL(recordedBlob)
        setAudioState({ ...audioState, blobURL, isRecording: false });
      }).catch((e) => console.log(e));
  };

  const handlePlaySelectedTracks = () => {
    console.log('PLAY SELECTED TRACKS')
    Object.keys(playAudioTrackList)
      .filter(key => playAudioTrackList[key])
      .map(key => new Audio(apiBaseUrl + getTrackByID(key).url))
      .forEach((audio, i) => {
        console.log('STARTING AUDIO ' + (i + 1), Date.now())
        audio.play()
      })
  }

  const handleDeleteSelectedTracks = async () => {
    console.log('DELETE SELECTED TRACKS')
    Object.keys(deleteTrackList)
      .filter(key => Boolean(deleteTrackList[key]))
      .map(key => getTrackByID(key))
      .forEach(async (deleteTrack) => {
        const urlParts = deleteTrack.url
          .split('/')
        const deleteParams = `${urlParts[2]}_${urlParts[3]}`
        console.log('DELETE PARAM', deleteParams)
        const response = await axios.delete(`${apiBaseUrl}/api/files/${deleteParams}`)
        console.log('DELETED FILE: ' + deleteTrack.url, response)
        const deleteDB = await axios.delete(apiBaseUrl + '/api/tracks/' + deleteTrack.id)
        console.log('DELETED TRACK: ', deleteDB)
      })
    setTracks(tracks.filter(track => !Object.keys(deleteTrackList).includes(track.id)))
  }


  return (
    <div className="container">
      
      <div id='divider' />
      <div>
        <div className="row">
          <div className="col">
            <div style={{ display: (videoMode === 'recording') ? 'block' : 'none' }}>
              <video id="recording" width="270" height="200" autoPlay muted ></video>
            </div>
            <div style={{ display: (videoMode === 'playLocal') ? 'block' : 'none' }}>
              <video id="playLocal" width="270" height="200" controls ></video>
            </div>
            <div style={{ display: (videoMode === 'playRemote') ? 'block' : 'none' }}>
              <video id="playRemote" width="270" height="200" autoPlay ></video>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col">
            <img src={videoRecordButton} style={{ width: "100%", objectFit: "cover" }} alt="Record" onClick={() => handleStartVideoRecording()} />
          </div>
          <div className="col">
            <img src={videoStopButton} style={{ width: "100%", objectFit: "cover" }} alt="Stop" onClick={() => handleStopVideoRecording()} />
          </div>
          <div className="col" >
            <a id="downloadButton" href={downloadButton.href} download={downloadButton.download}>
              <img src={videoDownloadButton} style={{ width: "100%", objectFit: "cover" }} alt="Download" />
              <p>Download</p>
            </a>
          </div>
        </div>
      </div>
      <div id='divider' />
      <div className="row">
        <audio id='audioRecorder' src={audioState.blobURL} controls="controls" />
      </div>
      {<div className="row">
        <button onClick={handleStartAudioRecording} disabled={audioState.isRecording} className='halfButtons' style={{ backgroundColor: 'red' }}>
          REC
            </button>
        <button onClick={handleStopAudioRecording} disabled={!audioState.isRecording} className='halfButtons'>
          STOP
            </button>
      </div>}

      <br />

      {blob && <div>
        <div className="row">
          <div className="col">
            SONG
        </div>
          <div className="col">
            <select
              value={trackInfo.productionId}
              onChange={({ target }) => setTrackInfo({ ...trackInfo, productionId: target.value })}>
              <option>valitse</option>
              {productions.map(production => <option key={production.id} value={production.id} >{production.name}</option>)}
            </select>
          </div>
        </div>
        <div className="row">
          <div className="col">
            <span>TRACK NAME</span>
          </div>
          <div className="col">
            <input
              id='trackNameInput'
              type='text'
              value={trackInfo.trackName}
              onChange={({ target }) => setTrackInfo({ ...trackInfo, trackName: target.value })}
            />
          </div>
        </div>
        {notification
          && <div className="row">
            <div style={{ textAlign: 'center', width: '100%', color: 'white', backgroundColor: notification.type === 'error' ? 'red' : 'green' }}>{notification.message}</div>
          </div>}
        <div className="row">
          <button type='button' className='largeButton' onClick={() => handleUploadRecording()}>UPLOAD</button>
        </div>
      </div>}

      <div id='divider' />
      <div className="row">
        <div className="col">
          <h5>PLAYER</h5>
        </div>
      </div>
      <div className='row'>
        <div className="col">
          SONGS
        </div>
        <div className="col">
          <select
            value={playProduction}
            onChange={({ target }) => setPlayProduction(target.value)}
          >
            <option>valitse</option>
            {productions.map(production => <option key={production.id} value={production.id} >{production.name}</option>)}
          </select>
        </div>
      </div>
      {playProduction && getProductionsTracks(playProduction).length > 0 &&
        <div>
          <div className='row'>
            <div className="col">
              VIDEO TRACKS
          </div>
            <div className="col">
              <select
                value={playVideoTrack}
                onChange={({ target }) => {
                  setPlayVideoTrack(target.value) 
                }}
              >
                <option>choose</option>
                {tracks
                  .filter(track => track.productionId == playProduction && isVideo(track))
                  .map(track => <option key={track.id} value={track.id}>{trackLabel(track)}</option>)}
              </select>
            </div>
          </div>
          {playVideoTrack && <div className='row'>
            <button type='button' className='largeButton' onClick={() => handlePlayRecording(playVideoTrack)}>PLAY VIDEO</button>
          </div>}
        </div>}
      {playProduction && getProductionsTracks(playProduction).length > 0
        ? <div>
          <div className='row'>
            <div className='col'>AUDIO TRACKS</div>
          </div>
          <div className='row'>
            <table>
              <tbody>
                <tr>
                  <th>Name</th>
                  <th>Play</th>
                  <th>Delete</th>
                </tr>
                {tracks
                  .filter(track => track.productionId == playProduction && !isVideo(track))
                  .map(track => <tr key={track.id}>
                    <td>{trackLabel(track)}</td>
                    <td><input type='checkbox' checked={Boolean(playAudioTrackList[track.id])} onChange={() => setPlayAudioTrackList({ ...playAudioTrackList, [track.id]: !playAudioTrackList[track.id] })} /></td>
                    <td><input type='checkbox' checked={Boolean(deleteTrackList[track.id])} onChange={() => setDeleteTrackList({ ...deleteTrackList, [track.id]: !deleteTrackList[track.id] })} /></td>
                  </tr>)}
              </tbody>
            </table>
          </div>
          <div className='row'>
            <button type='button' className='halfButtons' onClick={handlePlaySelectedTracks}>PLAY</button>
            <button type='button' className='halfButtons' onClick={handleDeleteSelectedTracks}>DELETE</button>
          </div>
        </div>
        : <div>NO TRACKS</div>}
    </div>
  );
}

export default VideoControl;